// write expense
function addExpense(trx_amount: number, trx_cost: number, category: string, payment_method: string) {
  db.transact(
    db.tx.expenses[id()].update({
      trx_amount,
      trx_cost,
      category,
      payment_method,
      paid_on: Date.now()
    })
  )
}

